import {JetView, plugins} from "webix-jet";

export default class ApplicationMenu extends JetView {
	config(){
		const _ = this.app.getService("locale")._;
		const theme = this.app.config.theme;

		return {
			view: "sidebar",
			css: theme,
			collapsed: false,
		    multipleOpen: true,
			data:[
				{id: "entities", icon:"mdi mdi-timetable", value:"Entities", 
					data:[
						{ id: "tables.events?type=11", icon:"mdi mdi-circle", value: "Party"},
						{ id: "tables.events?type=12", icon:"mdi mdi-circle", value: "Product"}
					]
				},
				{id: "applications", icon:"mdi mdi-timetable", value:"Applications", 
					data:[
						{ id: "tables.events?type=21", icon:"mdi mdi-circle", value: "Accounting"},
						{ id: "tables.events?type=22", icon:"mdi mdi-circle", value: "Catalog"},
						{ id: "tables.events?type=23", icon:"mdi mdi-circle", value: "Marketing"},
						{ id: "tables.events?type=24", icon:"mdi mdi-circle", value: "Order"}
					]
				}
			],
		    onClick:{ 
		    	"mdi-timetable": function  (event, id, node) {
//		    		alert("ecchime!!");
		    	}
		    }			
		};
	}
	
	init(sidebar){
		
		this.use(plugins.Menu,{
			id:sidebar
//			urls:{
//				"customers":"customers?user=1/information"
//			}
		});
		
		this.on(this.app,"menu:toggle",() => {
			sidebar.toggle();
		});
		
//		sidebar.openAll();
	}
}